footer, header {
  align-items: center;
  background-color: $see-through-background;
  color: $homepage-text;
  display: flex;
  flex-direction: column;
  padding: $padding/3 $padding;
  @include above($small-screen) {
    flex-direction: row;
    justify-content: space-between; }

  a, a:visited {
    border: 0;
    color: $homepage-text;
    display: flex;
    align-items: center;
    &:hover {
      border: 0;
      color: rgba($gray, 1);
      transition: all $base-timing ease;
      text-shadow: 1px 0 0 $lightish-gray; } }
  ul {
    margin: 0;
    padding: 0; }
  li {
    display: inline-block;
    list-style: none;
    &:not(:first-child) {
      padding-left: $padding; } }
  nav {
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center; } }

header {
  #logo {
    font-family: $logo-font;
    font-size: $base-font-size * 1.5; }
  nav {
    font-size: 90%; } }

footer {
  margin-top: $padding*2;
  nav {
    font-size: 80%;
    padding: $padding/4 0; }
  #copy {
    font-size: 70%; }
  #contact p {
    display: none; } }
